/*==================== VARIABLES CSS ====================*/
:root {
  --header-height: 3rem;

  /*========== Colors ==========*/
  /* Change favorite color */
  --hue-color: 197; /*Purple 250 - Green 142 - Blue 230 - Pink 340*/

  /* HSL color mode */
  --first-color: hsl(var(--hue-color), 69%, 61%);
  --first-color-second: hsl(var(--hue-color), 69%, 61%);
  --first-color-alt: hsl(var(--hue-color), 57%, 53%);
  --first-color-lighter: hsl(var(--hue-color), 92%, 85%);
  --title-color: hsl(var(--hue-color), 8%, 15%);
  --text-color: hsl(var(--hue-color), 78%, 5%);
  --text-color-light: hsl(var(--hue-color), 8%, 65%);
  --input-color: hsl(var(--hue-color), 70%, 96%);
  --body-color: hsl(var(--hue-color), 10%, 94%);
  --container-color: #FFF;
  --success-color: #d1e7dd;
  --success-color-text: #00b4d8;
  --danger-color: #a4161a;
  --hover-color: #d62828;
  --color-logo: #031218;
  

  /*========== Font and typography 
  hsl(142, 40%, 60%)
  ==========*/
  --body-font: 'Nunito', sans-serif;;

  /* .5rem = 8px, 1rem = 16px, 1.5rem = 24px ... */
  --big-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1.125rem;
  --normal-font-size: .938rem;
  --small-font-size: .813rem;
  --smaller-font-size: .75rem;

  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;

  /*========== Margenes Bottom ==========*/
  /* .25rem = 4px, .5rem = 8px, .75rem = 12px ... */
  --mb-0-25: .25rem;
  --mb-0-5: .5rem;
  --mb-0-75: .75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;

  /*========== z index ==========*/
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}
body{
  font-family: 'Poppins', sans-serif;
}
/*========== Dark Theme variable ==========*/
body.dark-theme{
  /* HSL color mode */
  --first-color-second: hsl(var(--hue-color), 30%, 8%);
  --title-color: hsl(var(--hue-color), 8%, 95%);
  --text-color: #FFF;
  --input-color: hsl(var(--hue-color), 29%, 16%);
  --body-color: hsl(var(--hue-color), 78%, 5%);
  --container-color: hsl(var(--hue-color), 29%, 16%);
  --success-color: #d1e7dd;
  --hover-color: #d62828;
  --color-logo: #d62828;
}

/* Font size for large devices 
hsl(142, 60%, 60%)
*/
@media screen and (min-width: 968px) {
  :root {
      --big-font-size: 3rem;
      --h1-font-size: 2.35rem;
      --h2-font-size: 1.5rem;
      --h3-font-size: 1.25rem;
      --normal-font-size: 1rem;
      --small-font-size: 1.2rem;
      --smaller-font-size: .813rem;
  }
}

.content {
  display: flex;
  margin: 82px auto 32px;
  padding: 0 16px;
  max-width: 960px;
  flex-direction: column;
  align-items: center;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}

body{
  margin: 0 0 var(--header-height) 0;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  background-color: var(--body-color);
  color: var(--text-color);
}

h1,h2,h3,h4{
  color: var(--title-color);
  font-weight: var(--font-medium-bold);
}

ul{
list-style: none;
}
a{
text-decoration: none;
}
/*================= REUSABLE CSS CLASSES =================*/

.section{
padding: 2rem 0 4rem;
}

.section_title{
font-size: var(--h1-font-size);
}

.section_subtitle{
display: block;
font-size: var(--small-font-size);
margin-bottom: var(--mb-3);
}

.section_title,
.section_subtitle{
text-align: center;
}

.container{
  max-width: 768px;
  margin-left: var(--mb-1);
  margin-right: var(--mb-1);
}

.grid{
display: grid;
gap: 1.5rem;
}

.header{
width: 100%;
position: fixed;
bottom: 0;
left: 0;
z-index: var(--z-fixed);
background-color: var(--body-color);
}

/*========================NAV======================== */
.nav{
  padding: 0px;
  display: flex;
  align-self: stretch;
  -moz-box-align: center;
  align-items: center;
  -moz-box-pack: justify;
  justify-content: space-between;
}
.nav_logo{
  width: 30px;
  height: 30px;
  margin-left: 10px;
  border-radius: 1rem 1rem 1rem 1rem ;
  box-shadow: 0 -1px 4px rgb(187, 166, 166, 0.15);
}
.nav_toggle {
margin: 10px;
margin-right: 20px;
margin-bottom: 15px;
color: var(--title-color);
font-weight: var(--font-medium);
}
.nav_logo:hover{
/* color: #d62828;  */
  color: var(--hover-color);
}
.nav_toggle{
  font-size: 1.1rem;
  cursor: pointer;
}
.nav_toggle:hover{
  color: var(--hover-color);
}
@media screen and (max-width: 767px) {
  .nav_menu{
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color: var(--body-color);
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgb(187, 166, 166, 0.15);
    border-radius: 1.5rem 1.5rem 0 0 ;
    transition: .3s;
  }
}
.nav_list{
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
.nav_link{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: var(--text-color);
  font-weight: var(--font-medium);
}
.nav_link:hover{
color: var(--hover-color);
}
.nav_icon{
font-size: 1.2rem;
}
.nav_close{
  position: absolute;
  right: 2.4rem;
  bottom: .8rem;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--hue-color);
}
.nav_close:hover{
  color: var(--hover-color);
}
.show-menu{
bottom: 0;
}

@media screen and (min-width: 768px) {
  body{
    margin: 0;
  }
  .header{
    top: 0;
    bottom: initial;
    padding: 0 1rem;
  }
  .nav{
    margin: 0px auto;
    align-items: center;
    height: calc(var(--header-height) + 1.5rem);
    column-gap: 1rem;
  }
  .nav_icon,
  .nav_close,
  #nav-toggle{
    display: none;
  }
  .nav_list{
    display: flex;
    column-gap: 2rem;

  }
  .nav_menu{
    justify-content: center;
  }
}

/*=========BUTTON DARK/LIGHT  */
.nav_btns{
  width: auto;
  display: flex;
  -moz-box-pack: end;
  justify-content: flex-end;
}
.change-theme{
font-size: 1.25rem;
color: var(--title-color);
margin-right: var(--mb-1);
cursor: pointer;
}
.change-theme:hover{
color: var(--hover-color);
}
/*==========ESTILOS LOGO========== */
.cls-1{
  fill: var(--color-logo);
}
.cls-1:hover{
  fill: #d62828;
}
.cls-2{fill:#fff;}